.sidebarlibrary {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;  
  height:100%;
  background-color:whitesmoke;
  box-shadow: 2px 2px 50px rgb(160, 150, 150);
  overflow: scroll;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  opacity: 0;
  h2{
      padding: 2rem;
  }
}

.librarysong {
    display: flex;
    // flex-direction: column;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    transition: background 0.5s ease;
    img {
        width: 20%;
    }
    &:hover {
        background: rgb(221, 237, 241);
    }

}
.song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
}

*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155,0.5) transparent ;
}
*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar-track {
    background: transparent;
}
*::-webkit-scrollbar-thumb{
    scrollbar-color: rgba(155,155,155,0.5);
    border-radius: 20px;
    border: transparent;
}
.selected {
    background-color: rgb(255, 211, 211);
}
.active-library {
    transform: translateX(0%);
    opacity: 1;
}
@media screen and (max-width: 768px) {
  .sidebarlibrary{
      width: 100%;
  }
}